//Costum
$warm:#F58D23;
$grass:#539E43;
$deep:#6762A6;

.bg-warm {
    background-color: $warm;
}

.bg-grass {
    background-color: $grass;
}

.bg-deep {
    background-color: $deep;
}

$h2-color:#1736DC;

.h2-font {
    color: $h2-color;
    padding: 30px 0;
}



body {
    font-family: Microsoft JhengHei;
    background-color: hsl(205, 38%, 93.45%);
}

a,
a:hover {
    text-decoration: none;
}

.section-rotate {
    display: flex;
    justify-content: center;
    box-sizing: content-box;
    transform: rotate(4deg);
    width: 100%;
    margin-left: -50%;
    // margin-right: -173px;
    padding: 25px 50%;

    .section-rotate-fixed {
        transform: rotate(-4deg);
    }
}

.wrap {
    background-image: url(../img/geometric-3037028_1920.jpg);
    background-size: cover;
    background-position: right center;
    max-width: 1200px;
    margin: 0 auto;
    background-attachment: fixed;
    overflow-x: hidden;
}

section {
    margin: 100px 0;
}

.navbar-bg {
    background: rgba(0, 0, 0, 0.27);
    background-position: right;
    transform: rotate(4deg);
    max-width: 130%;
    height: 97px;
    margin-top: -53px;
    padding: 0 10px;
    box-sizing: content-box;

    @include pad {
        transform: rotate(6deg);
        margin-top: -55px;

    }

    @include iphone5 {
        transform: rotate(0deg);
        margin-top: -30px;

    }
}

.navbar {
    height: 40px;
    margin-top: -47px;

    .navbar-collapse {
        margin-top: 54px;

        @include pad {
            margin-top: 10px;

            a {
                background: rgba(225, 255, 255, .35);
            }

            .nav-item,
            .nav-link {

                color: rgba(0, 0, 0, .95);
            }

            .nav-item.active,
            .nav-link.active,
            .nav-item:hover,
            .nav-link:hover {
                color: rgba(0, 0, 0, .55);
            }

            li {
                padding: 3px 0;
            }

        }

    }

    .navbar-toggler {
        margin-top: 48px;
    }
}


.navbar-brand {
    margin-top: 40px;
    font-size: 30px;

    h1 {
        z-index: 2000;
    }

    @include pad {
        margin-top: 30px;
        font-size: 26px;
    }

    @include iphone5 {
        margin-top: 0px;
    }

}

.profile {
    margin-top: 50px;

    @include iphone5 {

        h1,
        h2,
        p {
            text-align: center;
        }

    }

}

.web-skills {
    .card-img-top {
        max-width: 151px;
        max-height: 139px;
    }

    ul {
        list-style: disc;
        list-style-position: inside;
    }

    @include d-991 {
        li {
            font-size: .8rem;
        }

    }

    @include pad {
        .col-md-4 {
            max-width: 400px;
            margin: 0 auto;
        }

        .card-body {
            padding: 10%;
        }

        li {
            font-size: .9rem;
        }

    }
}


.c-shadow {
    box-shadow: 1px 2px 6px rgba(0, 0, 0, .15); //!important;
}

.c-shadow:hover {
    box-shadow: 1px 2px 10px rgba(0, 0, 0, .3) !important;
}


.bg-gradient-white {
    background: -webkit-linear-gradient(white, transparent);
    background: -o-linear-gradient(white, transparent);
    background: -moz-linear-gradient(white, transparent);
    background: linear-gradient(white, transparent);
}

.bg-gradient-dark {
    bottom: 0px;
    background: -webkit-linear-gradient(rgba(0, 0, 0, .1), rgba(0, 0, 0, .5));
    background: -o-linear-gradient(rgba(0, 0, 0, .1), rgba(0, 0, 0, .5));
    background: -moz-linear-gradient(rgba(0, 0, 0, .1), rgba(0, 0, 0, .5));
    background: linear-gradient(rgba(0, 0, 0, .1), rgba(0, 0, 0, .5));
}

// animated
/* .animated {
    opacity: 0;
    transition: all .8s;
    transform: translateY(50px);
} */

.fadeIn {
    opacity: 1;
    transform: translateY(0);
}



.location-icon {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    border: 3px solid #000;

}







.hover-show {
    opacity: 0;
    transition: opacity .35s;

}

.hover-show-title,
.hover-show-text {
    opacity: 0;
    margin-bottom: 0px;
    transition: all .3s;
    transform: translateY(10px);
    padding-left: 5px;
}

.hover-show-title {
    transition-delay: .15s;
    font-size: 1rem;
    padding-top: 5px;
}

.hover-show-text {
    transition-delay: .25s;
    font-size: 14px;
    padding-bottom: 5px;
}

.show {
    opacity: 1;
    transform: translateY(0);

}


.scale {
    transform: scale(1);
    transition: .5s;
}

.scale:hover {
    transform: scale(1.05);
    transition: .5s;
}


// progress
$progress-height: 20px;


.introduce {
    p {
        font-size: .85rem;
        text-indent: 1.9rem;
        line-height: 1.5rem;
        font-weight: 200;
        font-family: "GenYoGothicTW-Light";
    }


}


#experience {

    ul,
    li {
        list-style: circle;
    }
}

.cd-timeline__content {
    font-size: .85rem;
}


.cd-timeline__date {
    color: $secondary;

    @include d-991 {
        color: rgba($secondary, .75);

    }
}

footer {
    a {
        color: rgba(225, 255, 255, .55)
    }

    a:hover {
        color: rgba(225, 255, 255, .85)
    }
}